// 这里是全局定义的
import { createStore } from 'vuex'
import { getMusicLyrics } from '@/request/api/songInfo.js'

export default createStore({
  // 数据保存在 vuex 中
  state: {
    // 用户信息对象
    user: {
      // id
      userid: 0,
      // 昵称
      nickname: '',
      // 头像
      avatarUrl: '',
      // 关注数
      follows: 0,
      // 粉丝数
      followeds: 0,
      // 发布动态数
      locationStatus: 0,
      // 个性标签
      signature: '',
      // 等级
      level: 0,
      // 累计听歌
      listenSongs: 0,
      // 性别
      gender: 1,
      // 是否为新用户
      newUser: false,
      // 省份
      province: 0,
      // 城市
      city: 0,
      // 生日(要转格式)
      birthday: 0,
      // 注册时间
      createTime: 0,
      // 创建歌单数
      playlistCount: 0,
      // 会员类型
      vipType: 0
    },
    // 搜索出来的歌手
    artist: [{
      // 歌手头像
      img1v1Url: "https://p1.music.126.net/CyICIF9Hblc_V1gwt4Mudw==/109951165644346951.jpg",
      // 歌手名字
      name: "刘惜君"
    }],
    // 当前左侧菜单选项
    nowMenu: '',
    // 是否显示登录框
    isLoginBox: false,
    // 搜索的关键词
    searchKey: '',
    // 请求回来的数据转存到本地的有效期默认统一为 10 分钟
    validityTime: 600000,
    // 是否显示顶部精品歌单
    isHighQualitySongList: true,
    // 是否显示网友精选歌单
    isSongListSelectedByNetizens: true,
    // 是否显示精品歌单列表
    isBoutiqueSongList: false,
    // 是否显示底部音乐播放组件
    isPlayBox: false,
    // 歌曲歌词
    lyricList: {},
    // 正在播放歌曲的当前时间(单位 s)
    currentTime: 0,
    // 播放列表
    playList: [
      {
        // 歌曲详细信息对象
        al: {
          id: 98919436,
          name: "我不知道2020",
          pic: 109951165509525800,
          picUrl: "https://p2.music.126.net/2YfxYXFb3S-4HPZcn5lJxg==/109951165509525799.jpg",
          pic_str: "109951165509525799"
        },
        // 歌曲 id
        id: 1499345116,
        // 歌名
        name: "我不知道2020",
        // 歌手
        ar: [{id: 166010, name: "梁博"}],
        // 歌曲时长
        dt: 377000
      }
    ],
    // 歌单名(播放中歌曲来源)
    songListName: '每日歌曲推荐',
    // 歌单 id(播放中歌曲来源)
    songListId: 0,
    // 播放列表下标
    playListIndex: 0,
    // 歌曲总时长(单位 s)
    duration: 0,
    // 当前歌词
    nowLrc: ''
  },
  getters: {
  },
  // mutations 负责更改这里存储的属性
  mutations: {
    // 当前左侧菜单选项
    updateNowMenu: function(state, value) {
      state.nowMenu = value
    },
    // LoginBox 的显示与隐藏
    updateIsLoginBox(state, value) {
      state.isLoginBox = value
    },
    // 关闭顶部精品歌单和网友精选歌单，打开精品歌单列表
    updateBoutiqueSongList(state, value) {
      state.isHighQualitySongList = !value
      state.isSongListSelectedByNetizens = !value
      state.isBoutiqueSongList = value
    },
    // 是否显示底部音乐播放组件
    updateIsPlayBox(state, value) {
      state.isPlayBox = value
    },
    // 切换歌曲时更新歌词
    updateLyricList(state, value) {
      state.lyricList = value
    },
    // 更新播放歌曲的当前时间
    updateCurrentTime(state, value) {
      state.currentTime = value
      // console.log('state.currentTime', state.currentTime);
    },
    // 更新播放列表
    updatePlayList(state, value) {
      state.playList = value
      // console.log('播放列表:', state.playList);
    },
    // 更新歌单名(歌曲来源)
    updateSongListName(state, value) {
      state.songListName = value
    },
    // 更新歌单 id(歌曲来源)
    updateSongListId(state, value) {
      state.songListId = value
    },
    // 更新播放列表下标
    updatePlayListIndex(state, value) {
      state.playListIndex = value
    },
    // 更新歌曲总时长
    updateDuration(state, value) {
      state.duration = value
      // console.log('state.duration', state.duration);
    },
    // 更新当前歌词
    updateNowLrc(state, value) {
      state.nowLrc = value
    }
  },
  // 获取数据属于异步请求，在 actions 操作
  actions: {
    // 获取歌曲歌词接口
    async getLyrics(context, id) {
      let res = await getMusicLyrics(id)
      // console.log('获取歌曲歌词：', res);
      // 保存到 vuex
      context.commit('updateLyricList', res.data.lrc)
    }
  },
  modules: {
  }
})
