// 封装 axios 请求
import axios from 'axios'
// create() 创建 axios 实例 service
const service = axios.create({
  // 请求基地址，后台启动了的服务器域名(需要先 node app.js 启动 NeteaseCloudMusicApi-master 文件夹的网易云服务器)
  baseURL: 'http://localhost:3000/',
  // 接口超时时间，返回的时间，一次请求的最长时间 请求数据量特别大的时候，浏览器容易请求超时 所以设置大点（一分钟）
  timeout: 1000 * 60
})

// 向外导出实例 service
export default service