// 导入 eventBus 模块
import bus from '@/components/home/eventBus.js'; // 导入 store 才可以修改里面的属性，若在计算属性中用 mapState 解构的是只读不可修改的 

import store from '@/store/index.js'; // 引入解构辅助函数

import { mapMutations, mapState } from 'vuex';
import { getHotSearch, getDefaultSearch, getSearch } from '@/request/api/home.js';
export default {
  data() {
    return {
      // 是否显示搜索盒子
      isSearchBox: false,
      // 热搜列表
      hotSearch: [],
      // 搜索框显示的默认搜索关键词
      defaultSearch: '',
      // 输入的搜索关键词
      searchKey: '',
      // 关键词数组
      keyWordList: [],
      // "查看全部四个字"
      lookAll: '查看全部',
      // 图标
      icons: [{
        id: 1,
        name: '#icon-shezhi',
        title: '设置'
      }, {
        id: 2,
        name: '#icon-xinfeng1',
        title: ''
      }, {
        id: 3,
        name: '#icon-jianshaominimize3',
        title: 'mini模式（Ctrl+M）'
      }, {
        id: 4,
        name: '#icon-zuixiaohua',
        title: '最小化'
      }, {
        id: 5,
        name: '#icon-suoxiao',
        title: '向下还原'
      }, {
        id: 6,
        name: '#icon-guanbi',
        title: '关闭'
      }]
    };
  },

  // 接受父组件的值
  props: {
    // 顶部是否显示歌名与当前歌词
    isTopShowSongName: 0
  },

  // 页面加载渲染完成，自动执行的方法
  async mounted() {
    // 获取搜索历史，获取保存在 localStorage 里的搜索历史，防止因点击删除键后删除掉了 keyWordList 而设为空数组
    this.keyWordList = JSON.parse(localStorage.getItem('keyWordList')) ? JSON.parse(localStorage.getItem('keyWordList')) : []; // 获取热搜列表

    let res = await getHotSearch();
    this.hotSearch = res.data.data; // 获取搜索框里的默认搜索关键词

    let res1 = await getDefaultSearch();
    this.defaultSearch = res1.data.data.showKeyword; // 获取保存在 localStorage 里的搜索历史，防止因点击删除键后删除掉了 keyWordList 而设为空数组

    this.keyWordList = JSON.parse(localStorage.getItem('keyWordList')) ? JSON.parse(localStorage.getItem('keyWordList')) : []; // 添加搜索盒子监听器

    window.addEventListener('click', this.otherClose);
  },

  beforeDestroy() {
    /* 组件销毁的时候要移除侦听器 */
    window.removeEventListener('click', this.otherClose);
  },

  methods: {
    // 点击历史记录进行搜索
    async searchHistory(item) {
      // 把搜索关键词传到 Vuex
      store.state.searchKey = item; // 把搜索关键词传到输入框

      this.searchKey = item; // 数据发送方通过 eventBus 把 searchKey 传给 Search 和 SearchSingle 组件

      bus.emit('searchKey', item); // 先重新添加再去重，并且限制 keyWordList 长度(固定历史记录个数)
      // unshift() 在数组开头添加元素，并且返回数组新长度

      this.keyWordList.unshift(item); // 去重 返回一个不重复的 Set 对象
      // console.log(...new Set(this.keyWordList));

      this.keyWordList = [...new Set(this.keyWordList)]; // 固定历史记录个数，切割数组函数 splice(index,howmany) ——> 删除从index位置开始的数，howmany为删除的个数

      if (this.keyWordList.length > 10) this.keyWordList.splice(this.keyWordList.length - 1, 1); // 保存到 localStorage：先 keyWordList 转化成 JSON 形式

      localStorage.setItem("keyWordList", JSON.stringify(this.keyWordList)); // 综合搜索

      let res = await getSearch(item);
      console.log(res); // 跳转到搜索页面的单曲模块

      this.$router.push('/Search'); // 关闭搜索盒子和歌曲详情页下降隐藏

      this.isSearchBox = false;
      this.goDown();
    },

    // 清空历史
    delHistory() {
      var bool = confirm("确定删除历史记录？");

      if (bool == true) {
        localStorage.removeItem('keyWordList');
        this.keyWordList = [];
      } else return;
    },

    // 显示与隐藏 searchBox
    changeIsSearchBox() {
      // this.updateIsSearchBox(false)
      this.isSearchBox = !this.isSearchBox;
    },

    // 显示小叉号
    spanKeyMouseover(index) {
      this.$refs.delete[index].style.display = 'inline-block';
    },

    // 隐藏小叉号
    spanMouseleave(index) {
      this.$refs.delete[index].style.display = 'none';
    },

    // 显示所有关键词
    allKeyWordList() {
      this.$refs.keyWordList.style.height = 'auto';
      this.lookAll = '';
    },

    // 从关键词数组中删除对应关键词
    deleteKeyWord(index) {
      // console.log(index, this.keyWordList[index]);
      // splice(index,n) 删除以下标index为起点的n个元素。(之后的元素会往前移动)
      this.keyWordList.splice(index, 1);
      localStorage.setItem("keyWordList", JSON.stringify(this.keyWordList)); // console.log(this.keyWordList);
      // this.isSearchBox = true
      // '查看全部' 功能恢复
      // this.lookAll = '查看全部'
      // this.$refs.keyWordList.style.height = '76px'
    },

    // 输入关键词后回车搜索
    async enterKey() {
      // 前后去空格
      this.searchKey = this.searchKey.trim();

      if (this.searchKey !== '') {
        // 数据发送方通过 eventBus 把 searchKey 传给 Search 和 SearchSingle 组件
        bus.emit('searchKey', this.searchKey); // console.log('正在搜索：' + this.searchKey + '...');
        // unshift() 在数组开头添加元素，并且返回数组新长度

        this.keyWordList.unshift(this.searchKey); // 去重 返回一个不重复的 Set 对象
        // console.log(...new Set(this.keyWordList));

        this.keyWordList = [...new Set(this.keyWordList)]; // 固定历史记录个数

        if (this.keyWordList.length > 10) {
          // 切割数组函数 splice(index,howmany) ——> 删除从index位置开始的数，howmany为删除的个数
          this.keyWordList.splice(this.keyWordList.length - 1, 1);
        } // 保存到 localStorage：先 keyWordList 转化成 JSON 形式


        localStorage.setItem("keyWordList", JSON.stringify(this.keyWordList)); // 把搜索关键词传到 Vuex

        store.state.searchKey = this.searchKey; // 综合搜索

        let res = await getSearch(this.searchKey); // 关闭 SearchBox 窗口

        this.isSearchBox = false; // 跳转到搜索页面

        this.$router.push('/Search'); // 歌曲详情页下降隐藏

        this.goDown();
      }
    },

    // 显示搜索框 input 输入框获取焦点
    focus() {
      this.isSearchBox = true;
    },

    // 点击其它地方，搜索盒子与账号功能盒子关闭
    otherClose(e) {
      // 需要打开了搜索盒子才能使用 searchBox.contains() 方法，所以要加上 this.isSearchBox == true 条件
      if (this.isSearchBox == true && this.$refs.searchBox.contains(e.target)) {// console.log('在搜索盒子中');
      }

      if (this.isSearchBox == true && !this.$refs.searchBox.contains(e.target) && e.target != this.$refs.inputref) {
        // console.log('正在点击其它地方!关闭搜索盒子');
        this.isSearchBox = false;
      }

      if (this.isAccountBox == true && this.$refs.accountBox && this.$refs.accountBox.contains(e.target)) {// console.log('在账号功能盒子中！');
      }

      if (this.isAccountBox == true && this.$refs.accountBox && !this.$refs.accountBox.contains(e.target) && e.target != this.$refs.accountBoxChange1 && e.target != this.$refs.accountBoxChange2) {
        // console.log('正在点击其它地方!关闭账号功能盒子');
        this.isAccountBox = false;
      }
    },

    // 歌曲详情页下降隐藏
    goDown() {
      // 调用 PlayBox.vue(父组件 SongDetail.vue的兄弟组件)的 hideDetail() 方法
      // 传值给 PlayBox.vue 以触发它的方法
      bus.emit('hideDetail', 1);
    },

    // 在 methods 中解构 store 里的 index.js 里的 mutations 传过来的方法
    ...mapMutations(['updateIsSearchBox'])
  },
  computed: { ...mapState(['playList', 'playListIndex', 'nowLrc'])
  },
  watch: {
    isSearchBox() {
      // 每次重新打开都能显示 '查看全部' 以及收起更多历史
      // console.log('isSearchBox 改变了');
      this.lookAll = '查看全部';
      this.$refs.keyWordList.style.height = '76px';
    },

    // 顶部是否显示歌名与当前歌词
    isTopShowSongName() {
      // console.log('SongDetailTopNav 组件的 isTopShowSongName 改变为：', this.isTopShowSongName);
      this.$refs.topMiddle.style.opacity = this.isTopShowSongName == 1 ? 0 : 1;
      this.$refs.songNameBox.style.opacity = this.isTopShowSongName == 1 ? 1 : 0;
      this.$refs.songNameBox.style.transform = this.isTopShowSongName == 1 ? 'translateY(0)' : 'translateY(25px)';
    }

  }
};