import { createApp } from 'vue'
import App from './App.vue'
import router from './router'
import store from './store'
// 导入 vue-ls
import Storage from 'vue-ls'
// 导入含有公共方法的自定义插件
import MyPlugin from './myPlugin/index';
// 导入 vant 组件库的组件懒加载
import { Lazyload, RadioGroup, Radio } from 'vant';
// import getVant from './plugins/index.js'

// "vue-ls" 是用于方便地操作本地存储（Local Storage）的插件。提供方法来读取、写入和删除本地存储中的数据。
// 注意，"vue-ls" 只是一个方便的封装，实际上仍使用浏览器提供的 localStorage API，因此若浏览器不支持本地存储或设置了相应的安全限制，"vue-ls" 也无法正常工作
// vue-ls 的配置
const options = {
  // key 键的前缀（随便起）
  namespace: 'vue-ls_',
  // 变量名称（随便起） 使用方式：Vue.变量名称 或 this.$变量名称 this.$ls
  name: 'ls',
  // 作用范围：local、session、memory
  storage: 'local'
}


// 创建 Vue 应用程序实例
const app = createApp(App)



// 注册组件懒加载 Toast 轻提示
app.use(Lazyload, {
  lazyComponent: true,
});
app.use(Radio).use(RadioGroup);
// getVant(App)

// 挂载到应用程序，.use(MyPlugin) 是安装自定义插件
app.use(store).use(router).use(Storage, options).use(MyPlugin).mount('#app')
