import { mapState } from "vuex";
import { postComment } from '@/request/api/songInfo';
export default {
  data() {
    return {
      // 是否显示编辑评论盒子
      isWriteCommentBox: 0,
      // 最大评论文字数
      maxlength: 140,
      // 评论剩余可输入长度
      leftover: 140,
      // 评论内容
      commentContent: ''
    };
  },

  methods: {
    // 发表评论
    async postComment() {
      // 生成当前时间的时间戳
      var timestamp = new Date().getTime();
      console.log(this.commentContent, this.commentContent.length, timestamp);
      const res = await postComment(1, 0, this.playList[this.playListIndex].id, this.commentContent, timestamp, sessionStorage.getItem("cookie")); // console.log('发送歌曲评论', res);
      // console.log('发送歌曲评论', res.data.comment);
      // 关闭盒子

      this.isWriteCommentBox = 0;
    }

  },
  watch: {
    // 评论剩余可输入长度随着输入内容增多而减短
    commentContent() {
      this.leftover = 140 - this.commentContent.length;
    }

  },
  computed: { ...mapState(['playList', 'playListIndex'])
  }
};