// 引入解构辅助函数
import { mapMutations, mapState } from 'vuex';
import { getUserPlaylist } from '@/request/api/personalHome';
export default {
  data() {
    return {
      choseColor: 'rgb(240, 240, 240)',
      // 侧边栏
      menuItems: [{
        id: 1,
        title: '发现音乐',
        rootPath: '/DiscoverMusic'
      }, {
        id: 2,
        title: '播客',
        rootPath: '/Podcast'
      }, {
        id: 3,
        title: '视频',
        rootPath: '/Video'
      }, {
        id: 4,
        title: '关注',
        rootPath: '/Focus'
      }, {
        id: 5,
        title: '直播',
        rootPath: '/Live'
      }, {
        id: 6,
        title: '私人FM',
        rootPath: '/Fm'
      }],
      // 是否展开创建歌单
      isCreateSongList: 0,
      // 是否展开收藏歌单
      isCollectSongList: 0,
      // 用户信息对象
      user: {},
      // 用户创建和收藏的歌单
      playlist: [],
      // 用户创建的歌单
      userCreateLists: [],
      // 用户收藏的歌单
      userCollectLists: [],
      // 用户喜欢的音乐歌单 id
      userLoveListId: 0,
      // 当前在我喜欢的音乐
      mineLoveAcive: 0,
      // 是否已登录
      isLogin: 0
    };
  },

  computed: { // 在 computed 计算属性解构 store 里的 index.js 里的 state 里的属性
    ...mapState(['nowMenu'])
  },

  mounted() {
    // 登陆后获取用户创建和收藏的歌单
    if (sessionStorage.getItem("userInfo")) {
      this.isLogin = 1;
      this.getUserPlaylist();
    }
  },

  methods: { // 在 methods 中解构 store 里的 index.js 里的 mutations 传过来的方法
    ...mapMutations(['updateNowMenu']),

    chose(e) {
      // 先重新置 0
      this.mineLoveAcive = 0; // console.log(e);
      // console.log(e.target.outerText);
      // 路由跳转

      if (e.target.outerText == '发现音乐') {
        this.$router.push('/DiscoverMusic');
      } else if (e.target.outerText == '播客') {
        this.$router.push('/Podcast');
      } else if (e.target.outerText == '视频') {
        this.$router.push('/Video');
      } else if (e.target.outerText == '关注') {
        this.$router.push('/Focus');
      } else if (e.target.outerText == '直播') {
        this.$router.push('/Live');
      } else if (e.target.outerText == '私人FM') {
        this.$router.push('/Fm');
      } else if (e.target.outerText == '我喜欢的音乐') {
        // 添加 active 类
        this.mineLoveAcive = 1; // 登录后去往用户喜欢的音乐歌单

        if (sessionStorage.getItem("userInfo")) this.goDetail(this.userLoveListId);
      } else if (e.target.outerText == '最近播放') {
        this.$router.push('/Lately');
      } else if (e.target.outerText == '我的收藏') {
        this.$router.push('/Collect');
      } // 更新当前左侧菜单选项


      this.updateNowMenu(e.target.outerText); // console.log('当前页面：' + this.nowMenu + ' 页面');
    },

    // 获取用户歌单(包括用户创建和收藏的)(不获取私密歌单)
    async getUserPlaylist() {
      this.user = JSON.parse(sessionStorage.getItem('userInfo')); // 获取用户歌单

      const res = await getUserPlaylist(this.user.userid); // console.log('获取用户歌单', res);

      this.playlist = res.data.playlist; // console.log('用户创建和收藏的歌单', this.playlist);
      // 分离歌单

      let tempCreateLists = this.playlist.filter(item => item.creator.nickname == this.user.nickname); // 先保存《我喜欢的音乐》id

      this.userLoveListId = tempCreateLists[0].id; // 再去掉《我喜欢的音乐》

      tempCreateLists.splice(0, 1);
      this.userCreateLists = tempCreateLists; // console.log('用户创建的歌单', this.userCreateLists);

      this.userCollectLists = this.playlist.filter(item => item.creator.nickname !== this.user.nickname); // console.log('用户收藏的歌单', this.userCollectLists);
    },

    // 去往歌单详情页
    goDetail(id) {
      this.$router.push({
        path: '/SongListDetail',
        query: {
          id: id,
          // 1歌单 2专辑
          type: 1
        }
      });
    }

  }
};