import bus from './eventBus';
import { mapMutations, mapState } from "vuex";
import { getSongUrl } from '@/request/api/songInfo';
export default {
  data() {
    return {
      // 展开音乐详情页
      shadowDisplay: 0,
      // 暂停与播放键
      playKeyDisplay: 1,
      // 四个图标
      icons: [{
        id: 1,
        name: '#icon-aixin',
        title: '喜欢'
      }, {
        id: 2,
        name: '#icon-favorite',
        title: '收藏'
      }, {
        id: 3,
        name: '#icon-xiazai',
        title: '下载'
      }, {
        id: 4,
        name: '#icon-zhuanfa',
        title: '分享'
      }],
      // 是否隐藏歌曲详情页
      isHideDetail: 0,
      // 监听当前时间的定时器
      interVal: 0,
      // 进度条的百分比进度
      percentage: 0,
      // 是否复制歌名
      isDoubleName: 0,
      // 歌名是否在移动中(配合节流/防抖事件)
      isSongNameMoving: 0
    };
  },

  computed: { ...mapState(['playList', 'playListIndex', 'currentTime', 'duration'])
  },

  // 写在 created 阶段（现成的解决方案）
  created() {
    // 接收从 SongDetailTopNav 传过来的 hideDetail
    bus.on('hideDetail', val => {
      this.isHideDetail = val;
    }); // 开启监听键盘弹起事件，空格键

    window.addEventListener('keyup', this.space, true);
  },

  async mounted() {
    // 获取音乐 url
    const res = await getSongUrl(this.playList[this.playListIndex].id); // console.log('获取音乐 url', res);
    // 获取歌词，使用全局的 store(vuex)，dispatch() 调用 store 里的 actions 异步请求

    this.$store.dispatch('getLyrics', this.playList[this.playListIndex].id); // 更新歌曲总时长，需要 audio 先获取完音源

    let _this = this;

    setTimeout(function () {
      _this.addDuration();
    }, 500); // console.log('歌名', this.playList[this.playListIndex].name);
  },

  methods: {
    // 歌曲详情页展示
    showDetail() {
      // 左侧上下滚动
      // 拼接法 添加类名 注意新拼接的类名前要有空格
      // this.$refs.playLeft.classList.value = this.$refs.playLeft.classList.value + ' leftDown'
      // 赋值法
      this.$refs.playLeft.classList = 'playLeft leftDown'; // 触发父组件的方法以调用兄弟组件的方法

      this.$emit('showDetail'); // 重新赋值才能使监听器监听成功

      this.isHideDetail = 0;
    },

    // 歌曲详情页隐藏
    hideDetail() {
      this.$refs.playLeft.classList = 'playLeft leftUp';
      this.$emit('hideDetail');
    },

    // 歌曲播放
    play() {
      this.playKeyDisplay = 0;
      bus.emit('isPlay', 1);
      this.$refs.audio.play(); // 歌曲播放的时候，定时器监听时间进行传值

      this.updateTime();
    },

    // 歌曲暂停
    pause() {
      this.playKeyDisplay = 1;
      bus.emit('isPlay', 0);
      this.$refs.audio.pause(); // 暂停则清除定时器

      clearInterval(this.interVal);
    },

    // 空格键播放与暂停
    space(e) {
      if (e.keyCode === 32) {
        if (this.playKeyDisplay == 1) {
          this.play();
        } else {
          this.pause();
        }
      }
    },

    // 定时器监听当前时间进行传值(也可使用 audio 的 timeupdate 事件)
    updateTime() {
      this.interVal = setInterval(() => {
        // audio 有 currentTime 属性
        this.updateCurrentTime(this.$refs.audio.currentTime);
      }, 1000);
    },

    // 切换歌曲
    goPlay(num) {
      // 切换后的歌曲下标
      let index = this.playListIndex + num; // 从第一首去到最后一首

      if (index < 0) index = this.playList.length - 1; // 从最后一首去到第一首
      else if (index == this.playList.length) index = 0; // 更新在播歌曲下标

      this.updatePlayListIndex(index);
    },

    // 更新歌曲总时长
    addDuration() {
      // 歌曲时长从 audio 获取：！bug：会设为上一首的时长
      // console.log('audio 总时长：', this.$refs.audio.duration);
      // this.updateDuration(this.$refs.audio.duration)
      // 歌曲时长从歌曲设定获取：
      if (this.playList[this.playListIndex].dt) this.updateDuration(this.playList[this.playListIndex].dt / 1000);
      if (this.playList[this.playListIndex].duration) this.updateDuration(this.playList[this.playListIndex].duration / 1000);
    },

    // 处理时长显示
    fixDuration(duration) {
      let min = parseInt(duration / 60) < 10 ? '0' + parseInt(duration / 60) : parseInt(duration / 60);
      let sec = parseInt(duration % 60) < 10 ? '0' + parseInt(duration % 60) : parseInt(duration % 60);
      return min + ':' + sec;
    },

    // 判断播放的歌曲是否是 VIP 歌曲
    isVipSong() {
      if (this.playList[this.playListIndex].fee == 1) {
        // fee: 0 免费/无版权，1 VIP 歌曲，4 购买专辑，8 非会员可免费播放低音质，会员可播放高音质及下载(fee 为 1 或 8 的歌曲均可单独购买 2 元单曲)
        // 歌曲暂停
        this.pause();
        return true;
      } else {
        return false;
      }
    },

    // 鼠标移入长的歌名，滚动显示全部
    moveSongName() {
      // 配合节流/防抖事件
      // if (this.$refs.songNameWordsSpan.offsetWidth > 150)
      if (this.isDoubleName && this.isSongNameMoving == 0) {
        // 歌名在移动中(配合节流/防抖事件)
        this.isSongNameMoving = 1; // 移动总时长(根据歌名长度决定移动时长)

        const moveTime = parseInt(this.$refs.songNameWordsSpan.offsetWidth * 0.024); // 一定要在移动之前设置 transition

        this.$refs.songNameWordsDiv.style.transition = `all linear ${moveTime}s`; // 向左移动一个歌名的距离和几个空格的距离

        const moveWidth = this.$refs.songNameWordsSpan.offsetWidth + this.$refs.nbsps.offsetWidth;
        this.$refs.songNameWordsDiv.style.transform = `translateX(-${moveWidth}px)`; // console.log(this.$refs.songNameWordsDiv.style.transition, '歌名是否在移动中：', this.isSongNameMoving);
        // 移动总时长之后恢复

        setTimeout(() => {
          // 歌名没在移动(配合节流/防抖事件)
          this.isSongNameMoving = 0; // 动画清除，歌名回到原位

          this.$refs.songNameWordsDiv.style.transition = this.$refs.songNameWordsDiv.style.transform = ''; // console.log(this.$refs.songNameWordsDiv.style.transition, '歌名是否在移动中：', this.isSongNameMoving);
        }, moveTime * 1000 + 500);
      }
    },

    ...mapMutations(['updateCurrentTime', 'updatePlayListIndex', 'updateDuration'])
  },
  watch: {
    // 是否隐藏歌曲详情页
    isHideDetail() {
      if (this.isHideDetail == 1) this.hideDetail();
    },

    // 监听 store 里的播放列表(第一种监听 store 的方法)
    playList() {
      // console.log('store 里的播放列表改变了!');
      // 先判断是否是 VIP 歌曲
      if (!this.isVipSong()) {
        this.$refs.audio.autoplay = true; // 歌曲播放

        this.play();
      } // 更换歌单且点击的是第一首歌(若监听的是播放列表下标则不行，因为下标都是 0)


      if (this.playListIndex == 0) {
        // 歌名长的则复制歌名
        this.isDoubleName = this.playList[this.playListIndex].name.length > 20 ? 1 : 0; // 获取歌词

        this.$store.dispatch('getLyrics', this.playList[this.playListIndex].id); // 更新歌曲总时长

        this.addDuration();
      }
    },

    // 监听 store 里的播放列表下标(若发生改变，则自动播放音乐且获取新歌词)(第二种监听 store 的方法)
    "$store.state.playListIndex": {
      handler(newVal, oldVal) {
        // console.log('旧值:', oldVal, '新值:', newVal);
        // 歌名长的则复制歌名
        this.isDoubleName = this.playList[this.playListIndex].name.length > 20 ? 1 : 0; // 如果歌名在移动的时候切换歌曲

        if (this.isSongNameMoving) {
          // 歌名没在移动(配合节流/防抖事件)
          this.isSongNameMoving = 0; // 动画清除，歌名回到原位

          this.$refs.songNameWordsDiv.style.transition = this.$refs.songNameWordsDiv.style.transform = '';
        } // 先判断是否是 VIP 歌曲


        if (!this.isVipSong()) {
          this.$refs.audio.autoplay = true; // 歌曲播放

          this.play(); // 获取歌词，dispatch() 调用 store 里的 actions 异步请求

          this.$store.dispatch('getLyrics', this.playList[this.playListIndex].id); // 更新歌曲总时长

          this.addDuration();
        }
      }

    },

    // 正在播放歌曲的当前时间，参数 newValue 表示 currentTime 改变后的新值
    currentTime(newValue) {
      this.percentage = this.currentTime / this.duration * 100 <= 100 ? this.currentTime / this.duration * 100 : 100; // console.log('歌曲播放至百分之' + this.percentage);
      // 这首播放完，播放下一首

      if (parseInt(newValue) == parseInt(this.duration)) {
        // 播完歌单的最后一首，则回到第一首
        if (this.playListIndex === this.playList.length - 1) {
          this.updatePlayListIndex(0);
        } else {
          this.goPlay(1);
        }
      }
    }

  }
};