// 引入解构辅助函数
import { mapMutations, mapState } from 'vuex';
import { getLoginKey, getLoginCreate, getLoginCheck, getStatus, getCaptcha, cellphonePasswordLogin, cellphoneCaptchaLogin, refreshLogin } from '@/request/api/loginBox.js';
export default {
  name: 'LoginBox',

  data() {
    return {
      // 关闭次数
      closeNum: 0,
      // 初始位置
      x: 600,
      y: 150,
      // 鼠标距离移动窗口左侧和顶部偏移量
      leftOffset: 0,
      topOffset: 0,
      // 是否移动
      isMove: false,
      // 显示哪个 body 
      body: 1,
      // 验证方式
      verification: 'password',
      // key
      unikey: '',
      qrurl: '',
      // 二维码图片 直接以 base64 形式渲染出来
      qrimgs: '',
      // 二维码状态
      qrCheckData: {},
      // 是否正在登录
      isLoging: false,
      // 二维码状态
      message: '',
      // 手机号
      phone: '',
      // 手机账号的密码
      password: '',
      // 手机号验证码
      captcha: '',
      // 用户信息对象
      user: {
        // id
        userid: 0,
        // 昵称
        nickname: '',
        // 头像
        avatarUrl: '',
        // 关注数
        follows: 0,
        // 粉丝数
        followeds: 0,
        // 发布动态数
        locationStatus: 0,
        // 个性标签
        signature: '',
        // 等级
        level: 0,
        // 累计听歌
        listenSongs: 0,
        // 性别
        gender: 1,
        // 是否为新用户
        newUser: false,
        // 省份
        province: 0,
        // 城市
        city: 0,
        // 生日(要转格式)
        birthday: 0,
        // 注册时间
        createTime: 0,
        // 创建歌单数
        playlistCount: 0,
        // 会员类型
        vipType: 0
      },
      // 登录返回的 cookie (长字符串)
      cookie: '',
      // 是否触发获取登录状态函数 getStatus()
      getStatusOk: 0
    };
  },

  async mounted() {
    // 生成时间戳(在该函数内可供多个函数使用)
    let timestamp = new Date().getTime(); // 获取二维码 key

    let res = await getLoginKey(timestamp);
    this.unikey = res.data.data.unikey; // console.log('unikey:', this.unikey);
    // 通过二维码 key 去获取二维码

    let res1 = await getLoginCreate(this.unikey, timestamp); // console.log(res1);

    this.qrurl = res1.data.data.qrurl;
    this.qrimgs = res1.data.data.qrimg; // 检测扫码状态
    // ！不登陆进入歌单后返回不能再登录，Request failed with status code 404 请求失败，请求所希望得到的资源未被在服务器上发现

    this.getCheck(); // 延迟 1s 后检测扫码状态
    // setTimeout(async () => {
    //   this.getCheck()
    // }, 1000);
  },

  // 计算属性
  computed: {
    //top与 left加上 px
    position() {
      return `top:${this.y}px;left:${this.x}px;`;
    }

  },
  methods: {
    // 拖拽窗口 CSDN博主「橡皮擦不掉的」，原文链接：https://blog.csdn.net/ZuoZuoDangerou/article/details/118191137
    //鼠标按下弹窗
    mousedown(event) {
      this.leftOffset = event.offsetX;
      this.topOffset = event.offsetY;
      this.isMove = true;
    },

    //鼠标移动弹窗
    mousemove(event) {
      // 鼠标按下时才触发
      if (!this.isMove) return;
      this.x = event.clientX - this.leftOffset;
      this.y = event.clientY - this.topOffset;
    },

    //鼠标抬起，停止 move
    mouseup() {
      this.isMove = false;
    },

    // 关闭该组件(弹窗)
    close() {
      this.updateIsLoginBox(false);
      this.closeNum++; // 不在登录，清除二维码定时器

      this.isLoging = false;
    },

    // 二维码的缩小变大
    codeMouseover() {
      this.$refs.code_box.classList.add('smaller');
      this.$refs.pic.classList.add('picMove');
    },

    codeMouseleave() {
      this.$refs.code_box.classList.remove('smaller');
      this.$refs.pic.classList.remove('picMove');
    },

    // 检测扫码状态(803 状态码下会返回 cookie)
    async getCheck() {
      let timestamp = new Date().getTime(); // 检测扫码状态

      let res = await getLoginCheck(this.unikey, timestamp); // console.log('getLoginCheck', res);
      // 存状态文字信息('二维码不存在或已过期' '等待扫码' '授权中' '授权登陆成功')

      this.message = res.data.message; // 存二维码状态

      this.qrCheckData = res.data; // 存返回的二维码 cookie(不是账号的 cookie)

      sessionStorage.setItem('qrCookie', res.data.cookie); // 正在登录

      this.isLoging = true; // 803 状态码下会返回真正的用户 cookie

      if (res.data.code == 803) sessionStorage.setItem('cookie', res.data.cookie);
    },

    // 刷新登录(！用不了)
    async loginRefresh() {
      let timestamp = new Date().getTime();
      const res = await refreshLogin(this.unikey, timestamp); // console.log(res);
    },

    // 获取登录状态
    async getStatus() {
      const res = await getStatus(sessionStorage.getItem("qrCookie")); // console.log('获取登录状态', res);
      // 登录状态，是否正在登录

      sessionStorage.setItem('isLoging', res.data.data.account.status); // 0
      // 用户 id

      this.user.userid = res.data.data.account.id; // 用户头像

      this.user.avatarUrl = res.data.data.profile.avatarUrl; // 用户昵称

      this.user.nickname = res.data.data.profile.nickname; // 用户发布动态数

      this.user.locationStatus = res.data.data.profile.locationStatus; // 存储用户信息对象(复杂类型需先转换成 JSON 字符串)

      sessionStorage.setItem('userInfo', JSON.stringify(this.user));
    },

    // 利用手机号和密码登录 返回：网络太拥挤，稍后再试。
    async cellphonePasswordLogin() {
      let res = await cellphonePasswordLogin(this.phone, this.password); // console.log(res);
    },

    // 获取手机号验证码
    async getCaptcha() {
      let res = await getCaptcha(this.phone); // console.log(res);
    },

    // 利用手机号和验证码登录 返回：网络太拥挤，稍后再试。
    async cellphoneCaptchaLogin() {
      let res = await cellphoneCaptchaLogin(this.phone, this.captcha); // console.log(res);
    },

    // 在 methods 中解构 store 里的 index.js 里的 mutations 传过来的方法
    ...mapMutations(['updateIsLoginBox'])
  },
  watch: {
    // 监听是否关闭 监听关闭次数 closeNum
    closeNum() {
      // 关闭后就重置为初始位置
      this.x = 600;
      this.y = 150;
      this.body = 1;
    },

    // 监听登录状态 isLoging 是否正在登录
    isLoging() {
      // 已登录
      if (sessionStorage.getItem('user') == true) retrurn; // 未登录则设置 3s 定时器

      if (sessionStorage.getItem('user') == null) {
        let timerIsLogin = setInterval(async () => {
          // 检测扫码状态
          this.getCheck(); // 取出二维码状态

          let code = this.qrCheckData.code; // 判断状态码 code，800为过期, 801为等待扫码, 802为待确认, 803为授权登录成功(返回 cookies)

          if (code == 800) {
            //二维码添加阴影表示失效
            this.$refs.mask.style.display = 'block';
            console.log('800', this.message);
          } // 100次(5分钟) '801 等待扫码' 后出现 '800 二维码不存在或已过期'
          else if (code === 801 && this.isLoging == true) console.log('801', this.message);else if (code === 802) console.log('802', this.message);else if (code === 803) {
            console.log('803', this.message); // 获取登录之后的 uid和用户详情以及登录状态

            this.getStatus(); //记录有用户已登录

            sessionStorage.setItem("user", true); // 用户登录成功后清除定时器

            clearInterval(timerIsLogin); // 关闭该组件 LoginBox.vue

            this.close(); // 延迟 1s 保存完用户信息后再刷新页面

            setTimeout(() => {
              this.$router.go(0);
            }, 1000);
          } // 不在登录就清除定时器
          else if (this.isLoging == false) {
            clearInterval(timerIsLogin); // console.log('清除定时器!');
          }
        }, 3000);
      }
    }

  }
};