// 导入 lodash 对象里的防抖函数
import debounce from 'lodash/debounce'; // 导入 eventBus 模块

import bus from '@/components/home/eventBus.js'; // 导入 store 才可以修改里面的属性，若在计算属性中用 mapState 解构的是只读不可修改的 

import store from '@/store/index.js'; // 引入解构辅助函数

import { mapMutations, mapState } from 'vuex';
import { getHotSearch, getDefaultSearch, getSearch, getSearchSuggest } from '@/request/api/home.js';
import { getUserLogout, getUserDetail, getUserAccount, getUserSubcount, getUserLevel, getUserBinding, DailySignin } from '@/request/api/loginBox.js';
export default {
  data() {
    return {
      // 是否登录(跟LoginBox组件的意义不一样)
      isLogin: 0,
      // 是否显示搜索盒子
      isSearchBox: false,
      // 是否显示账号功能盒子
      isAccountBox: false,
      // 搜索建议返回的结果
      searchSuggest: {},
      // 搜索结果数组
      searchList: [],
      // 热搜列表
      hotSearch: [],
      // 搜索框显示的默认搜索关键词
      defaultSearch: '',
      // 输入的搜索关键词
      searchKey: '',
      // 关键词数组
      keyWordList: [],
      // "查看全部四个字"
      lookAll: '查看全部',
      // 用户信息对象
      user: {
        // id
        userid: 0,
        // 昵称
        nickname: '',
        // 头像
        avatarUrl: '',
        // 关注数
        follows: 0,
        // 粉丝数
        followeds: 0,
        // 发布动态数
        locationStatus: 0,
        // 个性标签
        signature: '',
        // 等级
        level: 0,
        // 累计听歌
        listenSongs: 0,
        // 性别
        gender: 1,
        // 是否为新用户
        newUser: false,
        // 省份
        province: 0,
        // 城市
        city: 0,
        // 生日(要转格式)
        birthday: 0,
        // 注册时间
        createTime: 0,
        // 创建歌单数
        playlistCount: 0,
        // 会员类型
        vipType: 0
      },
      // 是否有用户 
      hasUser: 0,
      // 是否可后退
      isLeft: 0,
      // 是否可前进
      isRight: 0
    };
  },

  // 页面加载渲染完成，自动执行的方法
  async mounted() {
    // 检验是否已登录
    // if (sessionStorage.getItem('user') != null) this.isLogin = sessionStorage.getItem('user')
    // if (sessionStorage.getItem('user') == null) this.isLogin = false
    this.isLogin = sessionStorage.getItem('user') == null ? false : sessionStorage.getItem('user'); // 获取搜索历史，获取保存在 localStorage 里的搜索历史，防止因点击删除键后删除掉了 keyWordList 而设为空数组

    this.keyWordList = JSON.parse(localStorage.getItem('keyWordList')) ? JSON.parse(localStorage.getItem('keyWordList')) : []; // console.log(this.keyWordList);
    // 获取搜索盒子里的热搜列表

    let res = await getHotSearch(); // console.log('获取热搜列表', res);

    this.hotSearch = res.data.data; // console.log('热搜列表', this.hotSearch);
    // 获取搜索框里的默认搜索关键词

    let res1 = await getDefaultSearch();
    this.defaultSearch = res1.data.data.showKeyword; // console.log('默认搜索关键词：', this.defaultSearch);
    // 获取保存在 localStorage 里的搜索历史，防止因点击删除键后删除掉了 keyWordList 而设为空数组

    this.keyWordList = JSON.parse(localStorage.getItem('keyWordList')) ? JSON.parse(localStorage.getItem('keyWordList')) : []; // 添加搜索盒子与账号功能盒子监听器

    window.addEventListener('click', this.otherClose); // 用户登陆后，本地存储有用户 id

    if (sessionStorage.getItem("userInfo")) {
      // 因为通过不同的接口调用，用户 Id 与动态数已在 LoginBox 中调用另一个接口获得并保存到本地中
      // JSON.parse() 从本地内存取出复杂类型
      this.user = JSON.parse(sessionStorage.getItem('userInfo')); // 获取用户信息(登录后调用)

      if (this.user.userid !== 0) this.getUserInfo(this.user.userid); // 签到
      // try {
      //   let res2 = await DailySignin(sessionStorage.getItem("cookie"))
      //   // console.log('登录', res2);
      // } catch(e) {
      //   console.log(e.name, '异常！');
      // }
    }
  },

  beforeDestroy() {
    /* ！组件销毁的时候要移除侦听器 */
    window.removeEventListener('click', this.otherClose);
  },

  methods: {
    // 监听搜索框，input 事件在文本框的内容发生变化时触发，包括输入、粘贴、剪切等操作。(debounce 防抖策略)
    input: debounce(function (keywords) {
      // 前后去空格
      this.searchKey = keywords.trim(); // 获取搜索建议

      if (this.searchKey !== '') this.getSearchSuggest();
    }, 1000),

    // 获取搜索建议
    async getSearchSuggest() {
      const res = await getSearchSuggest(this.searchKey); // console.log('获取搜索建议', res);

      this.searchSuggest = res.data.result; // console.log('搜索建议', this.searchSuggest);
    },

    // 获取用户信息(登录后调用)
    async getUserInfo(uid) {
      // 获取用户详情
      let res = await getUserDetail(uid, sessionStorage.getItem("cookie")); // console.log('getUserInfo', res);

      this.user.follows = res.data.profile.follows;
      this.user.followeds = res.data.profile.followeds;
      this.user.signature = res.data.profile.signature;
      this.user.level = res.data.level;
      this.user.listenSongs = res.data.listenSongs;
      this.user.gender = res.data.profile.gender;
      this.user.newUser = res.data.newUser;
      this.user.province = res.data.profile.province;
      this.user.city = res.data.profile.city;
      this.user.birthday = this.timestampToTime(res.data.profile.birthday);
      this.user.createTime = this.timestampToTime(res.data.profile.createTime);
      this.user.playlistCount = res.data.profile.playlistCount;
      this.user.vipType = res.data.profile.vipType; // console.log('用户信息对象', this.user);
      // 本地内存覆盖存储 LoginBox.vue 之前存储的 userInfo

      sessionStorage.setItem('userInfo', JSON.stringify(this.user)); // 用户信息保存到 vuex
      // ! bug：页面刷新后 vuex 又变为初始值

      store.state.user = this.user; // console.log('store 里的 user', store.state.user);
    },

    // 时间戳转日期，本文为 CSDN博主「一个橙子的奇遇记」，原文链接：https://blog.csdn.net/hong521520/article/details/105855514
    timestampToTime(timestamp) {
      //时间戳为 10位需 *1000，时间戳为13位的话不需乘 1000
      var date = new Date(timestamp);
      var Y = date.getFullYear() + '-';
      var M = (date.getMonth() + 1 < 10 ? '0' + (date.getMonth() + 1) : date.getMonth() + 1) + '-';
      var D = date.getDate() < 10 ? '0' + date.getDate() : date.getDate();
      return Y + M + D;
    },

    // 点击搜索历史记录
    async searchHistory(item) {
      // 搜索不一样的关键词 || 不在搜索页但是点击一样的关键词
      if (store.state.searchKey !== item || this.$route.path !== '/Search/SearchSingle') {
        // 把搜索关键词传到 Vuex
        store.state.searchKey = item; // 把搜索关键词传到输入框

        this.searchKey = item; // 把 searchKey 传给 Search 和 SearchSingle 组件

        bus.emit('searchKey', item); // 存到本地

        sessionStorage.setItem("searchKey", item); // 先重新添加再去重，并且限制 keyWordList 长度(固定历史记录个数)
        // unshift() 在数组开头添加元素，并且返回数组新长度

        this.keyWordList.unshift(item); // ...new Set(数组) 去重 返回一个不重复的 Set 对象

        this.keyWordList = [...new Set(this.keyWordList)]; // 固定历史记录个数，切割数组函数 splice(index, howmany) ——> 删除从 index 位置开始的数，howmany 为删除个数

        if (this.keyWordList.length > 10) this.keyWordList.splice(this.keyWordList.length - 1, 1); // 保存到本地 localStorage：先 keyWordList 复杂类型转化成 JSON 形式

        localStorage.setItem("keyWordList", JSON.stringify(this.keyWordList)); // 综合搜索
        // let res = await getSearch(item)
        // console.log('综合搜索', res);
        // 跳转到搜索页面的单曲模块(当前路由地址：this.$route.path)

        if (this.$route.path !== '/Search/SearchSingle') this.$router.push('/Search');
      } else {
        // 把 searchKey 传给 Search 和 SearchSingle 组件
        bus.emit('searchKey', item);
      } // 点击搜索的关键词是否跟上一个相同都要关闭搜索盒子


      this.isSearchBox = false;
    },

    // 点击搜索热搜榜
    searchHot(item) {
      // 关闭搜索盒子
      this.isSearchBox = false; // 把搜索关键词传到 Vuex

      store.state.searchKey = item; // 把搜索关键词传到输入框

      this.searchKey = item; // 把 searchKey 传给 Search 和 SearchSingle 组件

      bus.emit('searchKey', item); // searchKey 保存到本地

      sessionStorage.setItem('searchKey', item); // unshift() 在数组开头添加元素，并且返回数组新长度

      this.keyWordList.unshift(this.searchKey); // 去重 返回一个不重复的 Set 对象

      this.keyWordList = [...new Set(this.keyWordList)]; // 固定历史记录个数

      if (this.keyWordList.length > 10) this.keyWordList.splice(this.keyWordList.length - 1, 1); // keyWordList 保存到本地

      localStorage.setItem("keyWordList", JSON.stringify(this.keyWordList)); // 跳转到搜索页面的单曲模块(当前路由地址：this.$route.path)

      if (this.$route.path !== '/Search/SearchSingle') this.$router.push('/Search');
    },

    // 回车搜索输入的关键词
    async enterKey() {
      // 前后去空格
      this.searchKey = this.searchKey.trim();

      if (this.searchKey !== '') {
        // 把 searchKey 传给 Search 和 SearchSingle 组件
        bus.emit('searchKey', this.searchKey); // console.log('正在搜索：' + this.searchKey + '...');
        // unshift() 在数组开头添加元素，并且返回数组新长度

        this.keyWordList.unshift(this.searchKey); // 去重 返回一个不重复的 Set 对象
        // console.log(...new Set(this.keyWordList));

        this.keyWordList = [...new Set(this.keyWordList)]; // 固定历史记录个数

        if (this.keyWordList.length > 10) {
          // 切割数组函数 splice(index, howmany) ——> 删除从index位置开始的数，howmany为删除的个数
          this.keyWordList.splice(this.keyWordList.length - 1, 1);
        } // 保存到本地 localStorage：先 keyWordList 复杂类型转化成 JSON 形式


        localStorage.setItem("keyWordList", JSON.stringify(this.keyWordList)); // 把搜索关键词传到 Vuex

        store.state.searchKey = this.searchKey; // 综合搜索
        // let res = await getSearch(this.searchKey)
        // console.log(res);
        // this.searchList = res.data.result.songs
        // console.log(this.searchList);
        // 回车完后清空搜索框`
        // this.searchKey = ''
        // 关闭 SearchBox 窗口

        this.isSearchBox = false; // 跳转到搜索页面

        this.$router.push('/Search');
      }
    },

    // 清空历史
    delHistory() {
      var bool = confirm("确定删除历史记录？");

      if (bool == true) {
        localStorage.removeItem('keyWordList');
        this.keyWordList = [];
      } else {
        return;
      }
    },

    // 显示与隐藏 searchBox
    changeIsSearchBox() {
      this.isSearchBox = !this.isSearchBox;
    },

    // 显示小叉号
    spanKeyMouseover(index) {
      this.$refs.delete[index].style.display = 'inline-block';
    },

    // 隐藏小叉号
    spanMouseleave(index) {
      this.$refs.delete[index].style.display = 'none';
    },

    // 显示所有关键词
    allKeyWordList() {
      this.$refs.keyWordList.style.height = 'auto';
      this.lookAll = '';
    },

    // 从关键词数组中删除对应关键词
    deleteKeyWord(index) {
      // console.log(index, this.keyWordList[index]);
      // splice(index, n) 删除以下标index为起点的n个元素(之后的元素会往前移动)
      this.keyWordList.splice(index, 1);
      localStorage.setItem("keyWordList", JSON.stringify(this.keyWordList)); // console.log(this.keyWordList);
      // this.isSearchBox = true
      // '查看全部' 功能恢复
      // this.lookAll = '查看全部'
      // this.$refs.keyWordList.style.height = '76px'
    },

    // 刷新总页面
    refresh() {
      // 利用路由刷新页面
      // this.$router.go(0);
      // 利用 location 对象的方法刷新页面(参数为 true 则强制刷新)
      location.reload(true); // console.log('location 对象：', location);
    },

    // 显示登录框
    showLoginBox() {
      this.updateIsLoginBox(true);
    },

    // 显示账号功能盒子
    async showAccountBox() {
      this.isAccountBox = !this.isAccountBox;
    },

    // 显示搜索框 input 输入框获取焦点
    focus() {
      this.isSearchBox = true; // console.log('获取了焦点 isSearchBox：' + this.isSearchBox);
    },

    // input 输入框失去焦点
    blur() {// this.isSearchBox = false
      // console.log('失去了焦点 isSearchBox：' + this.isSearchBox);
    },

    // 上一页面
    previous() {
      // this.$router.go(-1)
      this.$router.back(); // console.log(this.$route.path);
      // console.log(this.$router);
    },

    // 下一页面
    next() {
      this.$router.go(1);
    },

    // 点击其它地方，搜索盒子与账号功能盒子关闭
    otherClose(e) {
      // 需要打开了搜索盒子才能使用 searchBox.contains() 方法，所以要加上 this.isSearchBox == true 条件
      if (this.isSearchBox == true && this.$refs.searchBox.contains(e.target)) {// console.log('在搜索盒子中');
      }

      if (this.isSearchBox == true && !this.$refs.searchBox.contains(e.target) && e.target != this.$refs.inputref) {
        // console.log('正在点击其它地方!关闭搜索盒子');
        this.isSearchBox = false;
      }

      if (this.isAccountBox == true && this.$refs.accountBox && this.$refs.accountBox.contains(e.target)) {// console.log('在账号功能盒子中！');
      }

      if (this.isAccountBox == true && this.$refs.accountBox && !this.$refs.accountBox.contains(e.target) && e.target != this.$refs.accountBoxChange1 && e.target != this.$refs.accountBoxChange2) {
        // console.log('正在点击其它地方!关闭账号功能盒子');
        this.isAccountBox = false;
      }
    },

    // 退出登录
    async logout() {
      let res = await getUserLogout(); // console.log(res);
      // 清空 sesstion 所有数据

      sessionStorage.clear(); // 移除 sesstion 指定数据
      // sessionStorage.removeItem("a");
      // 路由跳转/刷新页面

      this.$router.go(0);
    },

    // 去往个人主页
    gotoPersonalHome() {
      if (this.user.userid !== 0) {
        this.$router.push({
          path: '/PersonalHome',
          query: {
            userId: this.user.userid
          }
        });
      }
    },

    // 去往用户关注/粉丝页
    goFollows(id, type, nickname, limit) {
      // 关闭盒子
      this.isAccountBox = 0;
      this.$router.push({
        path: '/Follows',
        query: {
          id: id,
          // 1关注 2粉丝
          type: type,
          nickname: nickname,
          limit: limit
        }
      });
    },

    // 去往用户动态页
    goTrends(id, nickname, limit) {
      // 关闭盒子
      this.isAccountBox = 0;
      this.$router.push({
        path: '/Trends',
        query: {
          id: id,
          nickname: nickname,
          limit: limit
        }
      });
    },

    // 去往歌手页
    goSingerPage(id) {
      this.isSearchBox = false;
      this.$router.push({
        path: '/SingerPage',
        query: {
          id: id
        }
      });
    },

    // 去往歌单详情页
    goPlayListDetail(id) {
      // 关闭 SearchBox 窗口
      this.isSearchBox = false; // 去往歌单详情页

      this.$router.push({
        path: '/SongListDetail',
        query: {
          id: id,
          // 1歌单 2专辑
          type: 1
        }
      });
    },

    // 去往专辑详情页
    goAlbumDetail(id) {
      this.isSearchBox = false;
      this.$router.push({
        path: '/SongListDetail',
        query: {
          id: id,
          type: 2
        }
      });
    },

    // 在 methods 中解构 store 里的 index.js 里的 mutations 传过来的方法
    ...mapMutations(['updateIsLoginBox'])
  },
  watch: {
    isSearchBox() {
      // 每次重新打开都能显示 '查看全部' 以及收起更多历史
      // console.log('isSearchBox 改变了');
      this.lookAll = '查看全部';
      this.$refs.keyWordList.style.height = '76px';
    },

    // 后退与前进键的显隐
    '$route': function (newRoute, oldRoute) {
      // console.log('newRoute:', newRoute, 'oldRoute:', oldRoute);
      // console.log('window.history:', window.history);
      // console.log('window.history.state:', window.history.state);
      // 是否可后退、前进
      // this.isLeft = (typeof window.history.state.back !== 'undefined') ? 1 : 0
      this.isLeft = window.history.state.back ? 1 : 0;
      this.isRight = window.history.state.forward ? 1 : 0; // console.log(this.isLeft, this.isRight);
    }
  }
};