// 导入 axios 实例
import service from "..";

// 获取默认搜索关键词
export function getDefaultSearch() {
  // 返回对象
  return service({
    method: 'GET',
    // 对应接口的服务器地址
    // 这里的 url 会拼接上 service 的 baseURL
    url:'/search/default'
  })
}

// 获取搜索建议(传入搜索关键词可获得搜索建议 , 搜索结果同时包含单曲 , 歌手 , 歌单信息)
export function getSearchSuggest(keywords) {
  return service({
    method: 'GET',
    url:`/search/suggest?keywords=${keywords}`
  })
}

// 综合搜索 
export function getSearch(data) {
  return service({
    method: 'GET',
    url: `/search/?keywords=${data}&type=1018`
  })
}

// 获取热搜列表
export function getHotSearch() {
  return service({
    method: 'GET',
    url: '/search/hot/detail'
  })
}

// 获取歌单详情，需传入歌单id
export function getPlaylistDetail(data) {
  return service({
    method: 'GET',
    url: `/playlist/detail?id=${data}`
  })
}

// 获取最近播放歌曲
export function getRecentSongs(cookie) {
  return service({
    method: 'GET',
    url: `/record/recent/song?cookie=${cookie}`
  })
}