// 导入 axios 实例
import service from "..";

// 二维码登录涉及到下面 3 个接口(务必带上时间戳，防止缓存)
// 获取二维码 key
export function getLoginKey(timestamp) {
  return service({
    method: 'GET',
    url: `/login/qr/key?timestamp=${timestamp}`
  })
}

// 生成二维码 通过 key去获取二维码，可选参数: qrimg 传入后会额外返回二维码图片 base64 编码(务必带上时间戳，防止缓存)
export function getLoginCreate(key, timestamp) {
  return service({
    method: 'GET',
    url: `/login/qr/create?qrimg=true&key=${key}&timestamp=${timestamp}`
  })
}

// 检测扫码状态(务必带上时间戳，防止缓存)
export function getLoginCheck(key, timestamp) {
  return service({
    method: 'GET',
    // 跨域请求加上 withCredentials=true
    url: `/login/qr/check?key=${key}&timestamp=${timestamp}&withCredentials=true`
    // url: `/login/qr/check?key=${key}&timestamp=${timestamp}`
  })
}

// 获取登录状态
export function getStatus(qrCookie) {
  return service({
    method: 'GET',
    url: `/login/status?cookie=${qrCookie}`
  })
}

// 刷新登录(加了 key与时间戳也用不了)
export function refreshLogin(key, timestamp) {
  return service({
    method: 'GET',
    url: `/login/refresh?key=${key}&timestamp=${timestamp}`
  })
}

// 退出登录
export function getUserLogout() {
  return service({
    method: 'GET',
    url: '/logout'
  })
}

// 获取用户详情(需登录)
export function getUserDetail(uid, cookie) {
  return service({
    method: 'GET',
    url: `/user/detail?uid=${uid}&cookie=${cookie}`
  })
}

// 获取账号信息(登录后携带 cookie 调用)
export function getUserAccount(cookie) {
  return service({
    method: 'GET',
    url: `/user/account?cookie=${cookie}`
  })
}

// 获取用户信息，歌单收藏，mv, dj 数量(登录后携带 cookie 调用)
export function getUserSubcount(cookie) {
  return service({
    method: 'GET',
    url: `/user/subcount?cookie=${cookie}`
  })
}

// 获取用户等级信息，包含当前登录天数，听歌次数，下一等级需要的登录天数和听歌次数，当前等级进度(登录后携带 cookie 调用)
export function getUserLevel(cookie) {
  return service({
    method: 'GET',
    url: `/user/level?cookie=${cookie}`
  })
}

// 获取用户绑定信息(登录后携带 cookie 调用)
export function getUserBinding(cookie) {
  return service({
    method: 'GET',
    url: `/user/binding?cookie=${cookie}`
  })
}

// 获取验证码
export function getCaptcha(phone) {
  return service({
    method: 'GET',
    url: `/captcha/sent?phone=${phone}`
  })
}

// 手机号密码登录
export function cellphonePasswordLogin(phone, password) {
  return service({
    method: 'GET',
    url: `/login/cellphone?phone=${phone}&password=${password}`
  })
}

// 手机号验证码登录
export function cellphoneCaptchaLogin(phone, captcha) {
  return service({
    method: 'GET',
    url: `/login/cellphone?phone=${phone}&captcha=${captcha}`
  })
}

// 签到(type: 签到类型 , 默认 0, 其中 0 为安卓端签到 ,1 为 web/PC 签到)
export function DailySignin(cookie) {
  return service({
    method: 'GET',
    url: `/daily_signin?cookie=${cookie}&type=1`
  })
}

// 云贝今日签到信息
export function YunbeiToday(cookie) {
  return service({
    method: 'GET',
    url: `/yunbei/today?cookie=${cookie}`
  })
}