// 引入解构辅助函数
import { mapMutations, mapState } from 'vuex';
import bus from '@/components/home/eventBus';
export default {
  data() {
    return {
      // 歌曲是否在播放
      isPlay: 0,
      // 是否为会员歌曲
      isVip: 0,
      // 是否需要翻译
      isTranslate: 1,
      // 是否需要罗马音音译
      isTransliteration: 0,
      // 是否显示后侧功能栏
      isFunctionBar: 0,
      // 当前歌词
      nowLrc: ''
    };
  },

  methods: {
    // 去往来源歌单详情页
    goDetail() {
      // 收起音乐详情页，先给父组件发信息，然后调用兄弟组件 SongDetailTopNav.vue 的方法 goDown
      this.$emit('goDown1'); // this.songListId == 0 表示是 banner 的新歌首发；== 1 表示是'每日歌曲推荐'；== 2表示是'搜索单曲页'

      if (this.songListId == 0) {
        this.$router.push('/DiscoverMusic/DiscoverRecommend');
      } else if (this.songListId == 1) {
        this.$router.push('/RecommendSongs');
      } else if (this.songListId == 2) {
        this.$router.push('/Search/SearchSingle');
      } else {
        // 去往歌单详情页
        this.$router.push({
          path: '/SongListDetail',
          query: {
            id: this.songListId,
            // 1歌单 2专辑
            type: 1
          }
        });
      }
    },

    // 去往歌手页
    goSingerPage(id) {
      // 收起音乐详情页
      this.$emit('goDown1');
      this.$router.push({
        path: '/SingerPage',
        query: {
          id: id
        }
      });
    },

    // 去往专辑详情页
    goAlbumDetail(id) {
      // 收起音乐详情页
      this.$emit('goDown1');
      this.$router.push({
        path: '/SongListDetail',
        query: {
          id: id,
          // 1歌单 2专辑
          type: 2
        }
      });
    },

    // 在 methods 中解构 store 里的 index.js 里的 mutations 传过来的方法
    ...mapMutations(['updateNowLrc'])
  },

  // 写在 created 阶段（现成的解决方案）
  created() {
    // 接收从 PlayBox.vue 传过来的 isPlay
    bus.on('isPlay', val => {
      this.isPlay = val;
    });
  },

  // 计算属性
  computed: { ...mapState(['playList', 'playListIndex', 'songListName', 'songListId', 'lyricList', 'currentTime']),

    // 歌词
    lyric() {
      let arr;

      if (this.lyricList.lyric) {
        // 以换行符分割每一行
        arr = this.lyricList.lyric.split(/[(\r\n)\r\n]+/).map((item, i) => {
          // 每个 item 就是一行，分割每行的歌词内容
          let min = item.slice(1, 3);
          let sec = item.slice(4, 6);
          let mill = item.slice(7, 10);
          let lrc = item.slice(11, item.length); // 总的毫秒数

          let time = parseInt(min) * 60 * 1000 + parseInt(sec) * 1000 + parseInt(mill); // 三位的毫秒数：

          if (isNaN(Number(mill))) {
            mill = item.slice(7, 9);
            lrc = item.slice(10, item.length);
            time = parseInt(min) * 60 * 1000 + parseInt(sec) * 1000 + parseInt(mill);
          } // console.log(min, sec, mill, lrc);
          // 以对象形式返回


          return {
            min,
            sec,
            mill,
            lrc,
            time
          };
        });
        arr.forEach((item, i) => {
          if (i === arr.length - 1 || isNaN(arr[i + 1].time)) {
            // 最后一句歌词保持 100s ?
            item.pre = 100000;
          } else {
            // 还没到最后一句歌词，下一句歌词的开始时间等于上一句歌词的结束时间
            item.pre = arr[i + 1].time;
          }
        });
      } // console.log(arr);


      return arr;
    }

  },
  watch: {
    currentTime() {
      // 操作 DOM:
      // let p = document.querySelector("p.active");
      // console.log([p]);
      // 选择带有 active 类的那句歌词
      let temP = this.$refs.pLrc.filter(item => item.className == 'active'); // console.log(temP[0]);
      // 播放的歌词保持显示在区域中间

      if (temP[0] && temP[0].offsetTop > 150) this.$refs.lyric_bottom_left.scrollTop = temP[0].offsetTop - 150; // 获取当前歌词

      for (let i = 0; i < this.lyric.length; i++) {
        const currentTimeSec = this.currentTime * 1000;

        if (currentTimeSec >= this.lyric[i].time && currentTimeSec < this.lyric[i].pre && this.nowLrc !== this.lyric[i].lrc) {
          // 更新当前歌词
          this.nowLrc = this.lyric[i].lrc; // console.log(this.nowLrc);

          this.updateNowLrc(this.nowLrc);
        }
      }
    }

  }
};